import { Block } from '@ali/4ever-cangjie';
import TableRow from '../../mo/models/tableRow';
import TableCell from '../../mo/models/tableCell';
import createEmptyTableCell from './createEmptyTableCell';

export default function createTableRow(colSize: number, originalTableRow?: TableRow): TableRow {
  const nodes: TableCell[] = [];
  for (let i = 0; i < colSize; i += 1) {
    const data: TableCell['data'] = {};
    if (originalTableRow) {
      const originalCell = originalTableRow.nodes[i] as Block;
      if (originalCell) {
        const originalCellData = originalCell.data;
        if (originalCellData && originalCellData.bdr) {
          data.bdr = originalCellData.bdr;
        }
        if (originalCellData && originalCellData.fill) {
          data.fill = originalCellData.fill;
        }
      }
    }
    nodes.push(createEmptyTableCell(data));
  }
  return Block.create({
    type: 'table-row',
    nodes,
  }) as TableRow;
}
